import template from './template.html';
import UsesTranslator from '../../Mixins/usesTranslator';
import ActionHandler from '../../Mixins/actionHandler';
import {ApproveCommentHandler} from './Handlers';
import ActionState from '../../actionState';

require('./style.less');

export default {
  mixins: [UsesTranslator, ActionHandler],
  template: template,
  props: {
    comment: {
      type: Comment,
      default: null
    },
    permissions: {
      type: Object,
      default: null,
      required: true
    }
  },
  data() {
    return {
      currentAction: null
    };
  },
  methods: {
    cancelConfirmation() {
      // TODO: Reset action state.
      this.requiresConfirmation = false;
    },
    performAction() {
      console.log('yay!');
      this.requiresConfirmation = false;
    },
    test(comment) {
      this.confirm(new ApproveCommentHandler(comment))
        .onConfirm((state : ActionState) => {
          state.proceed();
        });
    }
  }
};
