import ActionState from '../../../actionState';
import trans from '../../../../trans';
import Comment from '../../../../Data/Comments/comment';

class ApproveCommentHandler extends ActionState {

  constructor(comment : Comment) {
    super();

    this.comment = comment;
    this.title = trans('actions.approve_confirm_title');
    this.activeTitle = trans('actions.approve_confirm_title_active');
    this.message = trans('actions.approve_confirm_message');
    this.progressMessage = trans('actions.approve_confirm_progress_message');
    this.abandonedMessage = trans('actions.approve_confirm_abandoned');
    this.errorMessage = trans('actions.approve_error_encountered');
    this.successMessage = trans('actions.approve_success');
  }

  proceedWith() {
    return this.comment.publish();
  }

}

export default ApproveCommentHandler;
