import Type from '../../Types/type';
import String from '../../Types/string';

class BaseResponse {

  constructor() {
    this.success = true;
    this.isRecoverable = false;
    this.errorCode = null;
    this.msg = '';
    this.error = null;
  }

  static fromApiResponse(apiResponse, err): BaseResponse {
    let response = new BaseResponse();

    response.success = Type.withDefault(apiResponse[BaseResponse.ApiSuccess], false);
    response.isRecoverable = Type.withDefault(apiResponse[BaseResponse.ApiIsRecoverable], true);
    response.errorCode = Type.withDefault(apiResponse[BaseResponse.ApiErrorCode], null);
    response.msg = String.withDefault(apiResponse[BaseResponse.ApiMessage], '');
    response.error = Type.withDefault(err, null);

    return response;
  }

}

BaseResponse.ApiSuccess = 'success';
BaseResponse.ApiIsRecoverable = 'is_recoverable';
BaseResponse.ApiMessage = 'msg';
BaseResponse.ApiErrorCode = 'error_code';

export default BaseResponse;
